<script>
import numberFormat from "@/helpers/numberFormat";
import axios from "axios";
import config from "@/data/config";
import { mapActions } from "vuex";
import PagePreloader from "@/components/PagePreloader.vue";
export default {
  props: ["id"],
  data() {
    return {
      count: 1,
      product: null,
      productLoading: false,
      productLoadingError: false,
      addingToCart: false,
    };
  },
  created() {
    this.getProduct();
  },
  methods: {
    getProduct() {
      this.productLoading = true;
      setTimeout(() => {
        axios(config.API_BASE_URL + "/api/products/" + this.$route.params.id)
          .then((rez) => {
            this.product = rez.data;
          })
          .catch((err) => {
            this.productLoadingError = true;
            console.log(err);
          })
          .finally(() => (this.productLoading = false));
      }, 1000);
    },
    colors() {
      return colors;
    },
    toCart() {
      this.addingToCart = true;
      this.addToCart({
        productId: this.product.id,
        quantity: this.count,
      }).then((rez) => {
        this.addingToCart = false;
      });
    },
    ...mapActions(["addToCart"]),
  },
  computed: {
    inCart() {
      return this.$store.state.cartProducts.find(
        (product) => product.productId === this.product.id
      );
    },
  },
  filters: {
    numberFormat,
  },
  watch: {
    "$route.params.id"() {
      this.getProduct();
    },
    immediate: true,
  },
  components: { PagePreloader },
};
</script>

<template>
  <main class="content container">
    <div class="content__top" v-if="product">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link :to="{ name: 'main' }" :class="'breadcrumbs__link'">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link :to="{ name: 'main' }" :class="'breadcrumbs__link'">
            {{ product.category.title }}
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> {{ product.title }} </a>
        </li>
      </ul>
    </div>
    <PagePreloader text="Загрузка товара..." v-if="productLoading" />
    <div v-else-if="productLoadingError">
      <p class="alert-message">Ошибка загрузки товара</p>
    </div>
    <section v-else class="item">
      <div class="item__pics pics">
        <div class="pics__wrapper">
          <img
            width="570"
            height="570"
            :src="product.image.file.url"
            :alt="product.title"
          />
        </div>
      </div>

      <div class="item__info">
        <span class="item__code">Артикул: {{ product.id }}</span>
        <h2 class="item__title">{{ product.title }}</h2>
        <div class="item__form">
          <form class="form" action="#" method="POST">
            <b class="item__price"> {{ product.price | numberFormat }} ₽ </b>

            <fieldset class="form__block">
              <legend class="form__legend">Цвет:</legend>
              <ul class="colors">
                <li
                  v-for="color in product.colors"
                  :key="color.id"
                  class="colors__item"
                >
                  <label class="colors__label">
                    <input
                      class="colors__radio sr-only"
                      type="radio"
                      name="color-item"
                      :value="color.id"
                    />
                    <span
                      class="colors__value"
                      :style="{ 'background-color': color.code }"
                    >
                    </span>
                  </label>
                </li>
              </ul>
            </fieldset>

            <fieldset class="form__block">
              <legend class="form__legend">Объемб в ГБ:</legend>

              <ul class="sizes sizes--primery">
                <li class="sizes__item">
                  <label class="sizes__label">
                    <input
                      class="sizes__radio sr-only"
                      type="radio"
                      name="sizes-item"
                      value="32"
                    />
                    <span class="sizes__value"> 32gb </span>
                  </label>
                </li>
                <li class="sizes__item">
                  <label class="sizes__label">
                    <input
                      class="sizes__radio sr-only"
                      type="radio"
                      name="sizes-item"
                      value="64"
                    />
                    <span class="sizes__value"> 64gb </span>
                  </label>
                </li>
                <li class="sizes__item">
                  <label class="sizes__label">
                    <input
                      class="sizes__radio sr-only"
                      type="radio"
                      name="sizes-item"
                      value="128"
                      checked=""
                    />
                    <span class="sizes__value"> 128gb </span>
                  </label>
                </li>
              </ul>
            </fieldset>

            <div class="item__row">
              <div class="form__counter">
                <button
                  type="button"
                  aria-label="Убрать один товар"
                  @click="count--"
                >
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-minus"></use>
                  </svg>
                </button>

                <input type="text" v-model.number="count" name="count" />

                <button
                  type="button"
                  aria-label="Добавить один товар"
                  @click="count++"
                >
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-plus"></use>
                  </svg>
                </button>
              </div>

              <button
                class="button button--primery"
                type="submit"
                @click.prevent="toCart"
                :disabled="inCart"
              >
                В корзину
              </button>
              <p v-if="addingToCart">Добавляем в корзину...</p>
              <p v-else-if="inCart">Товар в корзине</p>
            </div>
          </form>
        </div>
      </div>

      <div class="item__desc">
        <ul class="tabs">
          <li class="tabs__item">
            <a class="tabs__link tabs__link--current"> Описание </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#"> Характеристики </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#"> Гарантия </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#"> Оплата и доставка </a>
          </li>
        </ul>

        <div class="item__content">
          <p>
            Навигация GPS, ГЛОНАСС, BEIDOU Galileo и QZSS<br />
            Синхронизация со смартфоном<br />
            Связь по Bluetooth Smart, ANT+ и Wi-Fi<br />
            Поддержка сторонних приложений<br />
          </p>

          <a href="#"> Все характеристики </a>

          <h3>Что это?</h3>

          <p>
            Wahoo ELEMNT BOLT GPS – это велокомпьютер, который позволяет
            оптимизировать свои велотренировки, сделав их максимально
            эффективными. Wahoo ELEMNT BOLT GPS синхронизируется с датчиками
            по ANT+, объединяя полученную с них информацию. Данные отображаются
            на дисплее, а также сохраняются на смартфоне. При этом на мобильное
            устройство можно установить как фирменное приложение, так и
            различные приложения сторонних разработчиков. Велокомпьютер точно
            отслеживает местоположение, принимая сигнал с целого комплекса
            спутников. Эта информация позволяет смотреть уже преодоленные
            маршруты и планировать новые велопрогулки.
          </p>

          <h3>Дизайн</h3>

          <p>
            Велокомпьютер Wahoo ELEMNT BOLT очень компактный. Размеры устройства
            составляют всего 74,6 x 47,3 x 22,1 мм. что не превышает габариты
            смартфона. Корпус гаджета выполнен из черного пластика.
            На обращенной к пользователю стороне расположен дисплей диагональю
            56 мм. На дисплей выводятся координаты и скорость, а также
            полученная со смартфона и синхронизированных датчиков информация:
            интенсивность, скорость вращения педалей, пульс и т.д. (датчики не
            входят в комплект поставки). Корпус велокомпьютера имеет степень
            защиты от влаги IPX7. Это означает, что устройство не боится пыли, а
            также выдерживает кратковременное (до 30 минут) погружение в воду на
            глубину не более 1 метра.
          </p>
        </div>
      </div>
    </section>
  </main>
</template>
<style>
.catalog__overlay {
  position: relative;
}
