<script>
export default {
  props: ["label", "type", "name", "placeholder", "error", "value"],
  computed: {
    inputValue: {
      set(value) {
        this.$emit("input", value);
      },
      get() {
        return this.value;
      },
    },
  },
};
</script>
<template>
  <label class="form__label">
    <input
      class="form__input"
      :type="type"
      :name="name"
      :placeholder="placeholder"
      v-model="inputValue"
    />
    <span class="form__value">{{ label }}</span>
    <span class="form__error" v-if="error">{{ error }}</span>
  </label>
</template>
