<script>
import ProductFilter from "@/components/ProductFilter.vue";
import ProductList from "@/components/ProductList.vue";
import PagePagination from "@/components/PagePagination.vue";
import { mapState } from "vuex";
import PagePreloader from "@/components/PagePreloader.vue";

export default {
  data() {
    return {
      filter: {},
    };
  },
  created() {
    this.$store.dispatch("getProducts", { page: 1 });
  },
  computed: {
    ...mapState({ products: "products" }),
  },
  components: {
    ProductFilter,
    PagePagination,
    ProductList,
    PagePreloader,
  },
};
</script>

<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">Каталог</h1>
      <span class="content__info"> 152 товара </span>
    </div>
    <div class="content__catalog">
      <ProductFilter @filter="filterProducts($event)" />
      <section class="catalog">
        <div class="catalog__error" v-if="$store.state.productsErrorLoading">
          <p class="alert-message">
            Ошибка загрузки товаров<br />
            <a
              href="#"
              @click.prevent="$store.dispatch('getProducts', { page: 1 })"
              >Попробуйте еще раз</a
            >
          </p>
        </div>
        <PagePreloader
          text="Загрузка товаров..."
          v-if="$store.state.productsOverlay"
        />
        <ProductList :products="products" v-else="" />
        <PagePagination />
      </section>
    </div>
  </main>
</template>
<style>
.catalog {
  position: relative;
}
</style>
