<script>
import { mapGetters } from "vuex";

export default {
  methods: {
    gotoPage(pageId) {
      this.$store.dispatch("getProducts", { page: pageId });
    },
    getNext() {
      if (this.pagination.page < this.pagination.pages)
        this.$store.dispatch("getProducts", { page: this.pagination.page + 1 });
    },
    getPrev() {
      if (this.pagination.page > 1)
        this.$store.dispatch("getProducts", { page: this.pagination.page - 1 });
    },
  },
  computed: {
    ...mapGetters({ pagination: "getPagePagination" }),
  },
};
</script>

<template>
  <ul class="catalog__pagination pagination">
    <li class="pagination__item">
      <a
        class="pagination__link pagination__link--arrow"
        href="#"
        :class="{ 'pagination__link--disabled': pagination.page === 1 }"
        @click.prevent="getPrev()"
        aria-label="Предыдущая страница"
      >
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-left"></use>
        </svg>
      </a>
    </li>
    <li v-for="page in pagination.pages" class="pagination__item" :key="page">
      <a
        href="#"
        class="pagination__link"
        :class="{ 'pagination__link--current': page === pagination.page }"
        @click.prevent="gotoPage(page)"
      >
        {{ page }}
      </a>
    </li>
    <li class="pagination__item">
      <a
        class="pagination__link pagination__link--arrow"
        href="#"
        :class="{
          'pagination__link--disabled': pagination.page === pagination.pages,
        }"
        @click.prevent="getNext()"
        aria-label="Следующая страница"
      >
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-right"></use>
        </svg>
      </a>
    </li>
  </ul>
</template>
