<script>
import numberFormat from "@/helpers/numberFormat";
import { mapActions } from "vuex";
export default {
  props: ["item", "quantity"],
  filters: { numberFormat },
  computed: {
    amount: {
      get() {
        return this.quantity;
      },
      set(value) {
        if (value < 1) return false;
        this.$store.dispatch("changeProductQuantity", {
          productId: this.item.id,
          quantity: value,
        });
      },
    },
  },
  methods: {
    ...mapActions(["removeProduct"]),
  },
};
</script>

<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img
        :src="item.image.file.url"
        width="120"
        height="120"
        :alt="item.name"
      />
    </div>
    <h3 class="product__title">
      {{ item.name }}
    </h3>
    <p class="product__info">Объем: <span>128GB</span></p>
    <span class="product__code"> Артикул: {{ item.id }} </span>

    <div class="product__counter form__counter">
      <button
        type="button"
        aria-label="Убрать один товар"
        @click="amount = amount - 1"
      >
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>

      <input type="text" :value="quantity" name="count" />

      <button
        type="button"
        aria-label="Добавить один товар"
        @click="amount = amount + 1"
      >
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div>

    <b class="product__price">
      {{ (item.price * quantity) | numberFormat }} ₽
    </b>

    <button
      class="product__del button-del"
      type="button"
      aria-label="Удалить товар из корзины"
      @click="removeProduct({ productId: item.id })"
    >
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>
