<script>
import { RouterLink } from "vue-router";
import { mapGetters, mapMutations } from "vuex";
import format from "@/helpers/numberFormat";
import inputDefault from "@/components/inputDefault.vue";
import textareaDefault from "@/components/textareaDefault.vue";
import axios from "axios";
import config from "@/data/config";
import PagePreloader from "@/components/PagePreloader.vue";
export default {
  name: "orderPage",
  data() {
    return {
      formData: {},
      formErrors: {},
      formErrorMessage: "",
      preloader: false,
    };
  },

  computed: {
    ...mapGetters({ cart: "getCart", summ: "cartTotalPrice" }),
  },
  methods: {
    ...mapMutations({ updateOrder: "updateOrderData" }),
    numberFormat(number) {
      return format(number);
    },
    submitForm() {
      this.formErrorMessage = "";
      this.formErrors = {};
      this.preloader = true;
      axios
        .post(
          config.API_BASE_URL +
            "/api/orders?userAccessKey=" +
            this.$store.state.accessKey,
          this.formData
        )
        .then((rez) => {
          this.formErrors = {};
          this.$store.commit("resetCart");
          this.updateOrder(rez.data);
          this.$router.push({
            name: "success",
            params: { orderId: rez.data.id },
          });
        })
        .catch((err) => {
          this.formErrors = err.response.data.error.request || {};
          this.formErrorMessage = err.response.data.error.message;
        })
        .finally(() => {
          this.preloader = false;
        });
    },
  },
  components: { inputDefault, textareaDefault, PagePreloader },
};
</script>
<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <RouterLink :to="{ name: 'main' }" :class="'breadcrumbs__link'">
            Каталог
          </RouterLink>
        </li>
        <li class="breadcrumbs__item">
          <RouterLink :to="{ name: 'cart' }" :class="'breadcrumbs__link'">
            Корзина
          </RouterLink>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> Оформление заказа </a>
        </li>
      </ul>

      <h1 class="content__title">Корзина</h1>
      <span class="content__info"> {{ cart.length }} товара </span>
    </div>

    <section class="cart">
      <form
        class="cart__form form"
        action="#"
        method="POST"
        @submit.prevent="submitForm"
      >
        <PagePreloader text="Отправка заказа..." v-if="preloader" />
        <div class="cart__field" v-else>
          <div class="cart__data">
            <inputDefault
              :label="'ФИО'"
              :name="'name'"
              :placeholder="'Введите ваше полное имя'"
              :type="'text'"
              v-model="formData.name"
              :error="formErrors.name"
            />
            <inputDefault
              :label="'Адрес доставки'"
              :name="'address'"
              :placeholder="'Введите ваш адрес'"
              :type="'text'"
              v-model="formData.address"
              :error="formErrors.address"
            />
            <inputDefault
              :label="'Телефон'"
              :name="'phone'"
              :placeholder="'Введите ваш телефон'"
              :type="'tel'"
              v-model="formData.phone"
              :error="formErrors.phone"
            />
            <inputDefault
              :label="'Email'"
              :name="'email'"
              :placeholder="'Введи ваш Email'"
              :type="'email'"
              v-model="formData.email"
              :error="formErrors.email"
            />
            <textareaDefault
              :name="'comments'"
              v-model="formData.comments"
              :placeholder="'Ваши пожелания'"
              :label="'Комментарий к заказу'"
              :error="formErrors.comments"
            />
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="delivery"
                    value="0"
                    checked
                  />
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="delivery"
                    value="500"
                  />
                  <span class="options__value"> Курьером <b>500 ₽</b> </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="pay"
                    value="card"
                  />
                  <span class="options__value"> Картой при получении </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="pay"
                    value="cash"
                  />
                  <span class="options__value"> Наличными при получении </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="item in cart" :key="item.product.id">
              <h3>{{ item.product.title }}</h3>
              <b>{{ numberFormat(item.product.price) }} ₽</b>
              <span>Артикул: {{ item.product.id }}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>
              Итого: <b>{{ cart.length }}</b> товара на сумму
              <b>{{ numberFormat(summ) }} ₽</b>
            </p>
          </div>

          <button
            class="cart__button button button--primery"
            :disabled="cart.length === 0"
            type="submit"
          >
            Оформить заказ
          </button>
        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>
