<script>
import { RouterLink } from "vue-router";
import { mapState } from "vuex";
import format from "@/helpers/numberFormat";
import axios from "axios";
import config from "@/data/config";
export default {
  data() {
    return {
      cart: {},
    };
  },

  methods: {
    numberFormat(number) {
      return format(number);
    },
  },
  created() {
    axios(
      config.API_BASE_URL +
        "/api/orders/" +
        this.$router.currentRoute.params.orderId,
      {
        params: {
          userAccessKey: localStorage.getItem("userAccessKey"),
        },
      }
    )
      .then((rez) => {
        this.cart = rez.data;
      })
      .catch((err) => {
        console.log(err);
      });
  },
};
</script>
<template>
  <main class="content container" v-if="cart.id">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <RouterLink :to="{ name: 'main' }" :class="'breadcrumbs__link'">
            Каталог
          </RouterLink>
        </li>
        <li class="breadcrumbs__item">
          <RouterLink :to="{ name: 'cart' }" :class="'breadcrumbs__link'">
            Корзина
          </RouterLink>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> Оформление заказа </a>
        </li>
      </ul>

      <h1 class="content__title">
        Заказ оформлен <span>№ {{ cart.id }}</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
            Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет
            письмо с&nbsp;деталями заказа. Наши менеджеры свяжутся с&nbsp;Вами
            в&nbsp;течение часа для уточнения деталей доставки.
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key"> Получатель </span>
              <span class="dictionary__value">
                {{ cart.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key"> Адрес доставки </span>
              <span class="dictionary__value">
                {{ cart.address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key"> Телефон </span>
              <span class="dictionary__value"> {{ cart.phone }} </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key"> Email </span>
              <span class="dictionary__value"> {{ cart.email }} </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key"> Способ оплаты </span>
              <span class="dictionary__value"> картой при получении </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li
              class="cart__order"
              v-for="item in cart.basket.items"
              :key="item.id"
            >
              <h3>{{ item.product.title }}</h3>
              <b>{{ numberFormat(item.product.price) }} ₽</b>
              <span>Артикул: {{ item.product.id }}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>
              Итого: <b>{{ cart.basket.items.length }}</b> товара на сумму
              <b>{{ numberFormat(cart.totalPrice) }} ₽</b>
            </p>
          </div>
        </div>
      </form>
    </section>
  </main>
</template>
