import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";
import config from "@/data/config";
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    cartProducts: [],
    products: [],
    colors: [],
    categories: [],
    pagination: {},
    productsOverlay: false,
    productsErrorLoading: false,
    orderData: {},
    accessKey: null,
  },
  getters: {
    getPagePagination(state) {
      return state.pagination;
    },
    getCart(state) {
      return state.cartProducts.map((item) => {
        return {
          item: item,
          product: item.product,
        };
      });
    },
    cartTotalPrice(state, getters) {
      return getters.getCart.reduce(
        (ctx, el) => el.product.price * el.item.amount + ctx,
        0
      );
    },
  },
  mutations: {
    updateOrderData(state, order) {
      state.orderData = order;
    },
    resetCart(state) {
      state.cartProducts = [];
    },
    removeProduct(state, { productId }) {
      state.cartProducts = state.cartProducts.filter(
        (item) => item.productId !== productId
      );
    },
    setAmount(state, { productId, amount }) {
      const product = state.cartProducts.find(
        (item) => item.productId === productId
      );
      product.amount = amount;
    },
    addToCart(state, { productId, amount }) {
      const fProduct = state.cartProducts.find(
        (el) => el.productId === productId
      );
      if (fProduct) {
        fProduct.amount += amount;
      } else {
        state.cartProducts.push({
          productId,
          amount,
        });
      }
    },
    updateCart(state, cart) {
      state.cartProducts = cart.map((item) => ({
        productId: item.product.id,
        amount: item.quantity,
        product: item.product,
      }));
    },
    addAccessKey(state, key) {
      state.accessKey = key;
    },
  },
  actions: {
    changeProductQuantity(context, { productId, quantity }) {
      return axios
        .put(
          config.API_BASE_URL + "/api/baskets/products",
          {
            productId,
            quantity,
          },
          {
            params: {
              userAccessKey: context.state.accessKey,
            },
          }
        )
        .then((rez) => {
          context.commit("updateCart", rez.data.items);
        })
        .catch((error) => console.log(error));
    },
    removeProduct(context, { productId }) {
      return axios
        .delete(
          config.API_BASE_URL +
            "/api/baskets/products?userAccessKey=" +
            context.state.accessKey,
          {
            data: { productId },
          }
        )
        .then((rez) => {
          context.commit("removeProduct", { productId });
        });
    },
    getCart(context) {
      const aKey = localStorage.getItem("userAccessKey");
      return axios
        .get(config.API_BASE_URL + "/api/baskets", {
          params: {
            userAccessKey: aKey,
          },
        })
        .then((rez) => {
          if (!aKey) {
            localStorage.setItem("userAccessKey", rez.data.user.accessKey);
            context.commit("addAccessKey", rez.data.user.accessKey);
          } else if (!context.state.accessKey) {
            context.commit("addAccessKey", aKey);
          }

          context.commit("updateCart", rez.data.items);
        });
    },
    addToCart(context, { productId, quantity = 1 }) {
      return axios
        .post(
          config.API_BASE_URL +
            "/api/baskets/products?userAccessKey=" +
            context.state.accessKey,
          {
            productId,
            quantity,
          }
        )
        .then((rez) => {
          context.commit("updateCart", rez.data.items);
        })
        .catch((err) => console.log(err));
    },
    getCategories(context) {
      axios(config.API_BASE_URL + "/api/productCategories")
        .then((rez) => {
          context.state.categories = rez.data.items;
        })
        .catch((err) => console.log(err));
    },
    getColors(context) {
      axios(config.API_BASE_URL + "/api/colors")
        .then((rez) => {
          context.state.colors = rez.data.items;
        })
        .catch((err) => console.log(err));
    },
    getProducts(context, params) {
      context.state.productsOverlay = true;
      context.state.productsErrorLoading = false;
      const data = { limit: 6, page: params.page };
      if (typeof params.filter !== "undefined") {
        data.categoryId = params.filter.category;
        data.colorId = params.filter.color;
        data.minPrice = params.filter.priceFrom;
        data.maxPrice = params.filter.priceTo;
      }
      setTimeout(() => {
        return axios(config.API_BASE_URL + "/api/products", {
          params: data,
        })
          .then((rez) => {
            context.state.products = rez.data.items;
            context.state.pagination = rez.data.pagination;
          })
          .catch((err) => {
            context.state.productsErrorLoading = true;
            console.log(err);
          })
          .finally(() => (context.state.productsOverlay = false));
      }, 1000);
    },
  },
});
